import React, {Component} from "react";
import PropTypes from "prop-types";
import LabeledInput from "eshop/modules/core/components/hoc/LabeledInput";
import {connect} from "react-redux";
import {getCustomerDataForm} from "../../selectors";
import {
    changeCustomerCountry,
    changeCustomerDataFormField,
    changeCustomerDataFormFieldNoValidations
} from "../../actions/app";
import {setCountries} from "../../../cbs/actions/cbs";
import {requestCartCustomerData, requestRecalculateConsentsForForeigner} from "../../actions/data";
import {OraFloatingLabelSelect} from "eshop/components/OraFloatingLabelSelect";
import {
    getCountryName,
    getCountrySuggestions,
    getInputValidationParams,
    getPropsForIdentification,
    getPropsForInput,
    getPropsForSelect,
    mapCountrySuggestion,
    prepareForeignerIdentificationTypes
} from "../../utils/utils";
import DatePickerByDocumentTypeComponent from "../datepicker/DatePickerByDocumentTypeComponent";

export class MulticartCustomerDetailsView extends Component {

    constructor(props) {
        super(props);
        this.props.setCountries(this.props.foreignCountries);
    }

    componentDidMount() {
        this.props.requestCartCustomerData();
    }

    getPropsForPesel(name) {
        return {
            ...getPropsForInput(name, this.props, this.props.changeCustomerDataFormField, this.props.changeCustomerDataFormFieldNoValidations),
            errors: !this.props.noPesel && this.props.errors && this.props.errors[name],
            mask: '99999999999',
            placeholder: '___________'
        };
    }

    getPropsForCensoredPesel(name) {
        return {
            ...getPropsForInput(name, this.props, this.props.changeCustomerDataFormField, this.props.changeCustomerDataFormFieldNoValidations),
            valid: undefined
        };
    }

    getPropsForId(name, withMask) {
        let props = {
            ...getPropsForInput(name, this.props, this.props.changeCustomerDataFormField, this.props.changeCustomerDataFormFieldNoValidations),
        };
        if (withMask) {
            props.mask = 'aaa999999';
            props.placeholder = '______';
        }
        return props;
    }

    render() {
        return (this.props.show && !this.props.isBusinessClient) ?
        (
            <div className="opl-form l-table-row__wrapper">
                {this.props.show &&
                    <div>
                        <div className="l-row u-no-margin-top">
                            <div className="u-spacing-top-l l-col l-col-small-12 l-col-medium-6 l-col-4 u-no-padding-right u-no-padding-left u-small-no-padding">
                                {this.props.readOnly || this.props.existing ?
                                     <LabeledInput
                                        {...this.getPropsForCensoredPesel("pesel")}
                                        {...getInputValidationParams("pesel", this.props)}
                                     />
                                     :
                                     <LabeledInput
                                        {...this.getPropsForPesel("pesel")}
                                        {...getInputValidationParams("pesel", this.props)}
                                     />
                                 }
                            </div>

                            <div hidden={this.props.foreigner} className="u-spacing-top-l l-col l-col-small-12 l-col-medium-6 l-col-8 u-no-padding-right u-small-no-padding u-padding-left-l">
                                <LabeledInput
                                    {...this.getPropsForId("idNumber",true)}
                                    {...getInputValidationParams("idNumber", this.props)}
                                />
                            </div>

                            <div hidden={!this.props.foreigner} className="u-spacing-top-l l-col l-col-small-12 l-col-medium-6 l-col-8 u-no-padding-right u-small-no-padding u-padding-left-l">
                                <LabeledInput
                                    {...getPropsForInput('country', this.props, this.props.changeCustomerDataFormField, this.props.changeCustomerDataFormFieldNoValidations)}
                                    {...getInputValidationParams('country', this.props)}
                                    value={getCountryName(this.props.foreignCountries,this.props.country)}
                                    suggestions={getCountrySuggestions(this.props.foreignCountries,this.props.country)}
                                    mapSuggestion={mapCountrySuggestion}
                                    onBlur={this.props.changeCustomerCountry}
                                    autoComplete={true}
                                    minLength={1}
                                />
                            </div>
                        </div>
                        <div hidden={!this.props.foreigner} className="u-spacing-top-l l-row u-no-margin-top">
                            <div className="l-col-small-12 l-col-medium-12 l-col-12 u-no-padding-left u-small-no-padding u-spacing-l">
                                <OraFloatingLabelSelect
                                    {...getPropsForSelect('identification', this.props, this.props.changeCustomerDataFormField)}
                                    {...getInputValidationParams('identification', this.props)}
                                    className={"g-gray1-bg u-font-small"}
                                    options={prepareForeignerIdentificationTypes(this.props.foreignerIdentificationMap)}
                                    isFeedbackParam="true"
                                    withEmptyOption={false}
                                    index={this.props.index}
                                />
                            </div>

                        </div>

                        <div hidden={!this.props.foreigner} className="l-row u-no-margin-top">
                             <div className="l-col l-col-small-12 l-col-medium-6 l-col-4 u-no-padding-right u-no-padding-left u-small-no-padding u-spacing-l">
                                  <LabeledInput
                                        {...getPropsForIdentification('identificationNumber', true, this.props, this.props.changeCustomerDataFormField, this.props.changeCustomerDataFormFieldNoValidations)}
                                        {...getInputValidationParams('identificationNumber', this.props)}
                                  />
                             </div>
                            <div className="l-col l-col-small-12 l-col-medium-6 l-col-8 u-no-padding-right u-small-no-padding u-padding-left-l">
                                <DatePickerByDocumentTypeComponent
                                    {...this.props}
                                    onChange={this.props.changeCustomerDataFormField}
                                    startDateName="identificationRegistrationDate"
                                    endDateName="identificationExpirationDate"
                                />
                            </div>
                        </div>

                        <div hidden={!this.props.foreigner || !this.props.noPesel} className="l-row u-no-margin-top">
                            <div className="l-col l-col-small-12 l-col-medium-12 l-col-12 u-no-padding-right u-no-padding-left u-small-no-padding  u-spacing-l">
                                <OraFloatingLabelSelect
                                    {...getPropsForSelect('gender', this.props, this.props.changeCustomerDataFormField)}
                                    className="g-gray1-bg u-font-small"
                                    options={genderOptions}
                                    isFeedbackParam="true"
                                />
                            </div>
                        </div>
                    </div>
                }
            </div>
        ): <div></div>
    }
}


const genderOptions = [
    {value: 'MALE', description: 'Pan'},
    {value: 'FEMALE', description: 'Pani'}
];

MulticartCustomerDetailsView.propTypes = {
    pesel: PropTypes.string,
    idNumber: PropTypes.string,
    country: PropTypes.string,
    identification: PropTypes.string,
    identificationNumber: PropTypes.string,
    identificationExpirationDate: PropTypes.string,
    readOnly: PropTypes.bool,
    requestCartCustomerData: PropTypes.func,
    requestRecalculateConsentsForForeigner: PropTypes.func,
    changeCustomerDataFormField: PropTypes.func,
    disabledIdNumber: PropTypes.bool,
    foreigner: PropTypes.bool,
    foreignCountries: PropTypes.array,
    isSmsVerified: PropTypes.bool,
    isOnlineCookie: PropTypes.bool,
    foreignerIdentificationMap: PropTypes.oneOfType([
        PropTypes.shape({
            value: PropTypes.string,
            description: PropTypes.string
        }),
        PropTypes.arrayOf(PropTypes.shape({
            value: PropTypes.string,
            description: PropTypes.string
        }))
    ])
};

const MulticartCustomerDetailsComponent = connect(
        getCustomerDataForm,
        {
            requestCartCustomerData,
            changeCustomerDataFormField,
            changeCustomerDataFormFieldNoValidations,
            changeCustomerCountry,
            setCountries,
            requestRecalculateConsentsForForeigner
        }
    )(MulticartCustomerDetailsView);
export default MulticartCustomerDetailsComponent;
